<template>
  <div>
    <div class="top-wrapper">
      <div class="container">
        <h1>{{ title }}</h1>
        <p>{{ msg }}</p>
      </div>
      <div class="container">
        <div class="items">
          <item-component
              v-for="post in posts"
              v-bind:key="post.id"
              v-bind:icon="post.icon"
              v-bind:title="post.title"
              v-bind:link="post.link"
          ></item-component>
        </div>
      </div>
    </div>
    <work-page id='work' class=container></work-page>
    <contact-page id='contact' class=container></contact-page>
  </div>
</template>

<script>
import Item from '@/components/Item'
import ContactPage from '@/pages/ContactPage'
import WorkPage from '@/pages/WorkPage'
export default {
  components: {
    'item-component': Item,
    'work-page': WorkPage,
    'contact-page': ContactPage
  },
  name: 'Home',
  data () {
    return {
      title: 'Yuko Kanai\'s portfolio',
      msg: 'wellcome',
      posts: [
        {id: 1, title: 'Work', link: '/work', icon: 'fas fa-images'},
        {id: 2, title: 'Skill', link: '/skill', icon: 'fas fa-list-ul'},
        {id: 3, title: 'About', link: '/about', icon: 'fas fa-user'},
        {id: 4, title: 'Contact', link: '/contact', icon: 'fas fa-envelope'}
      ]
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
h1 {
  font-weight: normal;
  font-size: 60px;
}
h2::after {
  content: "";
  display: block;
  height: 1px;
  width: 30%;
  background-color: #ccc;
  margin: auto;
}
ul {
  list-style-type: none;
  padding: 0;
}
li {
  display: inline-block;
  margin: 5px 8px;
}
a {
  color: white;
  text-decoration: none;
}
header {
  height: 58px;
  width: 100%;
  background-color: rgba(14, 21, 22, 0.9);
  z-index: 1;
}
.container {
  padding: 0 15px;
  margin:0 auto;
}
.top-wrapper {
  padding: 100px 0 50px 0;
  background-image: url("../assets/keyboard-2308477_1920.jpg");
  background-size: cover;
  color: white;
  border-bottom: 2px solid #dee7ec;
}
.top-wrapper h1 {
  opacity: 0.8;
  font-size: 45px;
  letter-spacing: 3px;
}
.top-wrapper p {
  opacity:0.8;
}
.items {
  padding: 10px 0;
}
</style>



